.pagination {
    font-family: Arial, sans-serif;
    margin: auto;
    text-align: center;
    list-style: none;
}

.pagination li {
    display: inline;
}

.pagination li a {
    margin: 1px;
    padding: 5px 10px;
}

.pagination li.active a {
    background-color: #d6e0f0;
    color: #393b44;
}

.pagination li a:hover:not(.active) {
    background-color: #ddd;
}


